body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.side-nav{
	height:100%;
	background:#ccac9d;
	box-shadow:1px 0px 7px rgba(0,0,0,0.5);
	position:fixed;
	top:0;
	left:0;
	width:70%;
	max-width:400px;
	width:300px;
	z-index:200;
	transition: ease-out, -webkit-transform 0.5s;
	transition: transform 0.5s, ease-out;
	transition: transform 0.5s, ease-out, -webkit-transform 0.5s;
}

.close{
	-webkit-transform:translateX(-100%);
	        transform:translateX(-100%);
}



.nav{
 background:#ccac9d;
}



@media(max-width:769px){
  .nav{
    display:none;
  }
  .main{
    width:100%;
  }
}

@media(min-width:770px){
	.bar{
		display:none;
	}
	.side-nav{
		display:none;
	}
	.backdrop{
		display:none;
	}

}
.pic{
	border:solid 5px #ccac9d;
}

.sayHello{

  -webkit-animation-name: hello;
  -webkit-animation-duration: 10s;
}

@-webkit-keyframes hello {
  from {color:#FFFCEB; font-size:0px;}
  50% {color:#E2C3AA; font-size:100px;}
  to {color: #CCAC9D;}
}

input:focus {
    outline:none;
}
.App {
  text-align: center;
}

.scrollbar{
    overflow-y:scroll;
}

#style-1::-webkit-scrollbar {
    width: 0px;
} 

#style-1::-webkit-scrollbar-thumb {
    background-color: #ccac9d;
}

.icon i {
 font-size:6rem;
 margin:20px;
 padding:10px;
}

.backdrop {
	position:fixed;
	width:100%;
	height:100%;
	top:0;
	left:0;
	background:rgba(0,0,0,0.3);
	z-index:100;
}


